<template>
    <div class="modal-bank-overlay">
        <div class="container-create-review">
            <div class="close-button">
                <button @click="close()" class="close-modal-solution">X</button>
            </div>
            <div class="container-list-question">
                <div class="header-list-exams">
                    <h1>
                        Tạo đề ôn
                    </h1>
                    <div class="flex-end">
                        <button class="button" @click="typeCreate = 1">
                            Ngẫu nhiên
                        </button>
                        <button class="button" @click="typeCreate = 2">
                            Tự tạo
                        </button>
                    </div>
                </div>
                <div v-if="step == 1">
                    <div class="body-container-create-review" v-if="typeCreate == 1">
                        <h3>Chủ đề</h3>
                        <div class="container-choice-topic">
                            <div class="topic-level" v-for="topic in topics" :key="topic.id">
                                <VueMultiselect v-model="topic.name" :options="subjects ? subjects : []" :multiple="false"
                                    :close-on-select="true" :clear-on-select="true" :preserve-search="true"
                                    placeholder="Chủ đề" label="name" track-by="name" :preselect-first="true">
                                </VueMultiselect>
                                <VueMultiselect v-model="topic.level" :options="levels ? levels : []" :multiple="false"
                                    :close-on-select="true" :clear-on-select="true" :preserve-search="true"
                                    placeholder="Mức độ" label="name" track-by="name" :preselect-first="true">
                                </VueMultiselect>
                            </div>
                            <div class="add-topic-level" @click="addTopicLevel">
                                Thêm chủ đề
                            </div>
                        </div>
                        <div class="flex-end">
                            <button class="button" @click="move(-1)">
                                Trở lại
                            </button>
                            <button class="button" @click="move(1)">
                                OK
                            </button>
                        </div>
                    </div>
                    <div class="body-container-create-review" v-else>
                        <h3>Nội dung</h3>
                        <div class="container-choice-topic">
                            <LatexComponent :isUpdate="true" :content="content" @update="update" ref="dataUpload" />
                        </div>
                        <div class="flex-end">
                            <button class="button" @click="move(-1)">
                                Trở lại
                            </button>
                            <button class="button" @click="move(1)">
                                OK
                            </button>
                        </div>
                    </div>
                </div>
                <div class="body-container-create-review" v-else>
                    <div>
                        <QuestionComponent v-for="(question, index) in  questions" :key="index" :question="question"
                            :ref="'question_' + question.question.question_id" @create="createQuestion"
                            @update="updateQuestion()" @delete="deleteQuestion"
                            @addlistQuestionFollow="addlistQuestionFollow" :id="'question_' + question.question.question_id"
                            :index="startIndex + index" :type="2" isOwner="true" @seeSolution="seeSolution"
                            :listItemsSubject="listItemsSubject" />
                    </div>
                    <div class="flex-end">
                        <button class="button" @click="move(-1)">
                            Trở lại
                        </button>
                        <button class="button" @click="move(1)">
                            OK
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import LatexComponent from '@/components/LatexComponent.vue';
import QuestionComponent from '@/components/Question/QuestionComponent.vue';
import { ref } from 'vue';
import VueMultiselect from 'vue-multiselect'
import { upload } from '../../services/uploadReview'
export default {
    name: "ModalAddQuestionBank",
    components: {
        VueMultiselect,
        LatexComponent,
        QuestionComponent
    },
    setup() {
        const typeCreate = ref(1)
        const step = ref(1)
        const displayAdd = ref(false)
        const solution = ref({})
        const render = ref(true)
        return {
            typeCreate, step, displayAdd, solution, render
        }
    },
    watch: {
        selectAll() {
            this.displayAdd = !this.displayAdd
            if (this.selectAll[0]) {
                this.questions.forEach(question => {
                    this.selectQuestions.add(question.question.question_id)
                });
                console.log(this.selectQuestions)
            } else {
                this.selectQuestions = new Set()
            }
        },
        selectQuestions() {
            if (this.selectQuestions.size != 0) {
                this.displayAdd = true
            } else {
                this.displayAdd = false
            }
            console.log("Question Select")
            console.log(this.selectQuestions)
        }
    },
    computed: {

    },
    data() {
        return {
            content: '<p>Bài 1&nbsp;<math xmlns="http://www.w3.org/1998/Math/MathML"><munder><mi>lim</mi><mrow><mn>1</mn><mo>→</mo><mo>∞</mo></mrow></munder></math></p>',
            questions: [],
            subjects: [
                { name: 'Hình học', id: 2 },
                { name: 'Bất đẳng thức', id: 3 },
                { name: 'Tích phân', id: 4 }
            ],
            levels: [
                {
                    name: "Nhận biết",
                    id: 1
                },
                {
                    name: "Thông hiểu",
                    id: 2
                },
                {
                    name: "Vận dụng",
                    id: 3
                },
                {
                    name: "Vận dụng cao",
                    id: 4
                }
            ],
            topics: [{
                id: 1, name: { name: 'Hình học', id: 2 }, level: {
                    name: "Thông hiểu",
                    id: 2
                }
            },
            {
                id: 2, name: { name: 'Bất đẳng thức', id: 2 }, level: {
                    name: "Vận dụng cao",
                    id: 4
                }
            },],
            selectAll: [],
            selectQuestions: new Set(),
            listFilters: [{ id: 1, name: "OK" }],
            // questions: [
            //     { question: { question_id: 1, content: 'OK1', page: 1, type: 1, index: 1, result_id: "", contentResult: "", scope: 1, dependence_id: 1 } },
            //     {
            //         question: { question_id: 2, content: 'OK2', page: 1, type: 2, index: 1, result_id: "", contentResult: "", scope: 1, dependence_id: 1 },
            //         choices: [
            //             { id: 3, question_id: 2, content: '<p>Phương án A</p>' },
            //             { id: 4, question_id: 2, content: '<p>Phương án B</p>' },
            //             { id: 5, question_id: 2, content: '<p>Phương án C</p>' },
            //             { id: 6, question_id: 2, content: '<p>Phương án D</p>' }
            //         ]
            //     },
            //     {
            //         question: { question_id: 2, content: 'OK2', page: 1, type: 2, index: 1, result_id: "", contentResult: "", scope: 1, dependence_id: 1 },
            //         choices: [
            //             { id: 3, question_id: 2, content: '<p>Phương án A</p>' },
            //             { id: 4, question_id: 2, content: '<p>Phương án B</p>' },
            //             { id: 5, question_id: 2, content: '<p>Phương án C</p>' },
            //             { id: 6, question_id: 2, content: '<p>Phương án D</p>' }
            //         ]
            //     },

            // ]
        }
    },
    mounted() {
        this.handleGetData()
    },
    props: ['idSolution'],
    methods: {
        async handleGetData() {

        },
        close() {
            this.$emit('close')
        },
        addTopicLevel() {
            this.topics.push({
                id: 4, name: {
                    id: '', name: ''
                }, level: {
                    id: '',
                    name: ''
                }
            })
        },
        async move(distance) {
            if (distance == 1) {
                if (this.typeCreate == 2) {
                    let data = {
                        document: this.$refs.dataUpload['editorData']
                    }
                    const response = await upload(data)
                    this.questions = response.data
                    console.log(this.questions)
                }
                this.step = 2
                console.log("Next")
            } else {
                this.step = 1
            }


        }
    }
}

</script>
<style scoped>
.modal-bank-overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    color: white;
    background-color: #2b2929da;
    z-index: 100;
}

.container-create-review {
    margin: 0 22.5%;
    background-color: rgba(82, 113, 130, 0.495);
    height: 100%;
}

.container-list-question {
    padding-left: 35px;
    padding-right: 35px;
    overflow-y: hidden scroll;
}





.close-button {
    display: flex;
    justify-content: flex-end;
}

.close-modal-solution {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    cursor: pointer;
    background-color: rgb(248, 74, 5);
}

.flex-end {
    display: flex;
    justify-content: flex-end;
}

.button {
    margin-right: 2px;
    margin-left: 2px;
    margin-top: 2px;
    height: 35px;
    box-shadow: 0 8px #999;
    border-radius: 5px;
    border: 2px solid #ea4f4c;
    background-color: #222;
    color: white;
    text-align: center;
    justify-content: center;
    text-decoration: none;
    cursor: pointer;
}

.button:active {
    box-shadow: 0 5px #666;
    transform: translateY(4px);
}

.body-container-create-review {
    height: 650px;
    overflow-y: auto;
}

.container-choice-topic {
    height: 540px;
    overflow-y: auto;
}

.topic-level {
    display: flex;
    margin-top: 2px;
}

.add-topic-level {
    margin-top: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid #ea4f4c;
    background-color: #222;
    min-height: 35px;
    border-radius: 5px;
    cursor: pointer;
}

.add-topic-level:hover {

    background-color: #8b8b8b;

}

@import url('vue-multiselect/dist/vue-multiselect.css');
</style>